import { Shape } from './Shape'
import { ExtendedPoint as Point } from './Point'


/**
 *
 */
class Path extends Shape {

    /**
     *
     * @param {Array|number} arg0
     * @param arg1
     * @param arg2
     *
     * @example
     * var path = new Path([[3,5],[3,6]])
     */
    constructor(arg0, arg1, arg2) {
        let position = [0, 0]

        if (Array.isArray(arg0) && Array.isArray(arg0[0])) {
            position = arg0[0]
        }
        super(position)

        for (let segment of arg0) {
            this.appendSegment(new Path.Segment(segment))
        }
    }

    appendSegment(segment) {
        if (!(segment instanceof Path.Segment)) {
            segment = new Path.Segment(segment)
        }
        this.addChild(segment)
    }

    getSegments() {
        return this._children
    }
}

Path.Segment = class extends Shape {

    /**
     *
     * @param {Point} position
     * @param {Point} [handleIn]
     * @param {Point} [handleOut]
     */
    constructor(position, handleIn, handleOut) {
        super(position)
        this._handleIn = (handleIn) ? new Point(handleIn) : null
        this._handleOut = (handleOut) ? new Point(handleOut) : null

        if (this._handleIn)this.addChild(this._handleIn)
        if (this._handleOut)this.addChild(this._handleOut)
    }

    get position() {
        return this._position
    }

    set position(position) {
        this._position.set(position)
    }

    get handleIn() {
        return this._handleIn
    }

    set handleIn(handle) {
        this._handleIn.set(handle)
    }

    get handleOut() {
        return this._handleOut
    }

    set handleOut(handle) {
        this._handleOut.set(handle)
    }

    /**
     *
     * @returns {boolean}
     */
    hasHandleIn() {
        return (!!this._handleIn)
    }

    hasHandleOut() {
        return (!!this._handleOut)
    }
}

export { Path }
